'use strict'

const test = require('ava')
const uuid = require('uuid-base62')
const r = require('rethinkdb')
const Db = require('../')

const dbName = `senagram_${uuid.v4()}`
const db = new Db({ db: dbName })

test('guardar imagen', async t => {
  t.is(typeof db.guardarImagen, 'function', 'guardarImagen es una funcion')
})

test.before('configurar base de datos', async t => {
  await db.connect()
  t.true(db.connected, 'esta conectado')
})

test.after('desconectando base de datos', async t => {
  await db.disconnect()
  t.false(db.connected, 'esta desconectado')
})

test.after.always('borrar la base de datos', async t => {
  let conn = await r.connect({})
  await r.dbDrop(dbName).run(conn)
})
